        description1: "நல்ல தமிழில் அரபீ கற்றுக் கொடுக்கபடாததால், அரபீ அறிஞர்களில் அநேகரின் தமிழ் மொழி-மொழிபெயர்ப்புப் போன்றவைகளில் பெரும் தவறுகள் காணப்படுதலோடு அறபீ வாக்கியங்களின் தெளிவான கருத்துகளும் மறைபட்டிருக்கின்றன.தத்துவ, பூத, பௌதீக வியல்களும், சரித்திரம், வானவியல், புவியியல், ஏகேச்வரவாதம், வைத்தியம், தருக்கம், கலை, இலக்கியம், பொறியியல், இரசாயனம் முதலான முக்கிய அறிவுகளும் மதுரஸாக்களில் போதிய அளவில் கற்றுக் கொடுக்கப்படாமலிருந்தலால், அவைகளிற் காணப்படும் விசேட சொற்களிற்சில அர்த்தமற்றனவாய்ப் புழங்கப்படுதலோடு இவ்வறிவுகளும் புறக்கணிப்படுகின்றன.",
        description2: "இக்காரணங்களால் அரபீ மொழி ஞானம், இயற்றலறிவு, இஸ்லாமிய தத்துவ போதனை – சாதனை – அனுஷ்டானம், இவைகள் மிகமிகக் குறைந்து காணப்படுகிறமை கண்கூடு.ஆதலால், எம்மாற் கூடிய பிரயாசையின் பேரில் கேற்கண்ட குறைகளிற் சிலவற்றையேனும் சமாளிப்பான் வேண்டி, அறிவுவகை விகற்பங்களாற் பேதப்படும் அர்த்தங்களை அரபீ சொற்களுக்குச் சுத்தமான தமிழில் அரபீ எழுத்தில் எழுதுவதுடன் புதியனவும் புதியனவாய் நுழைந்தனவுமான அனேக அரபீ சொற்களின் அர்த்தங்களும் இதில் கொடுப்படுகின்றன.இதனால் தமிழ் முஸ்லிம்களின் அரபீ அபிவிருத்திக்கும், அரபீ தமிழுக்கும் புத்துயிரளிப்பதுடன் இஸ்லாமியக் கலையும் அபிவிருத்தியடைய ஏதுவாகிறது.",
