        description2:"பதில்:ஓரு குருவிடம் பேசிப்பார்க்கலாம். பேசும்போது உரியமாதிரி முறையோடு பேசவேண்டும். அப்போதுதான் தான் யாரென்பதைக் காட்டிக்கொடுப்பார் அல்லாமல் அதற்கு மாற்றமாய் போய் பேசினால் பொதுவாய் ஆலிம்களுடன் பேசுவதைப் போலிருக்கும். ஆலிமுடன் பேசுவதைப்போன்று பேசி அனுப்பிவிடுவார்கள். அறியவேண்டும் என்ற நோக்கத்தோடுச் சென்றால் அவர்களோடு பேசுவதைக் கொண்டு அவர்கள் ஞானம் தெரிந்தவர்கள், விளங்கியவர்கள் ஒரு செய்குகிற்கு பொருத்தமானவர்கள் எனக் கண்டுப்பிடித்துக் கொள்ளலாம். சிலநேரங்களில் தேட்டம் உள்ளவர்கள் மித்தம் விருப்பத்தோடு எனக்கு ஒரு காமிலான (பூரணமான) செய்கை (குருவை) நீ எனக்கு காட்டியருள்வாய் யா அல்லாஹ் என்று துவா (பிரார்த்தனை) செய்தால் கனவில் காட்டித்தருவதும் உண்டு. கனவில் காட்டப்படுவதுதான் மிகவும் நல்லது. படேஷா ஹஜ்ரத் இருந்தார்கள் (அவர்களை) மஞ்சுவி நாயகம் என்பார்கள். சிறந்த வலீயாக அவர்களுடைய காலத்தில் திகழ்ந்தார்கள். ஆனால் அவர்கள் அரபி படித்தவர்கள் அல்ல. ஒரு அலுவலகத்தில் அதிகாரியாக இருந்து வேலைப் பார்த்தவர்கள். திடிரென அவர்களுடைய நிலமையெல்லாம் பலவகையிலும் மாற்றங்கண்டு சில விசயங்களைச் சொல்லத் தொடங்கினார்கள். பொது மக்களுக்கு ஒரே குழப்பம் ஏற்பட்டுவிட்டது. இவரொரு உம்மி (படிப்பறிவு இல்லாதவர்) அரபியல்லாதவர் இவருக்கு என்னத் தெரியும் என்றெல்லாம் கூறினார்கள். ஆலிம்கள் அதற்கு எதிர்ப்பு காண்பித்தார்கள். மிகச்சிறந்த ஒரு ஆலிம் அப்துல் கரீம் என்பவர் அதற்கு (மஞ்சுவி நாயகத்திற்கு) பத்வாவும் கொடுத்து விட்டார். - இமாம் அஸ்சையது கலீல் அவுன் மௌலானா அவர்கள்.",
