        description1:"பூமிக்கே புனிதம் சேர்த்த மதீனாநகர் வாழும் – மனித இனத்துக்கே மாண்பு சேர்த்த மாநபி ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களின் பேசி முடியாப் பெருமைகளைப் போற்றிப் புகழும் கவிதை நூல் இது!இந்நூலின் ஆசிரியர் அஸ்ஸெய்யித் கலீல் அவ்ன் மௌலானா நாயகமவர்களிடம் புதுக்கவிதைப் பூபாளம், சாகித்திய அகாடமி விருது பெற்ற கவிஞர், மு.மேத்தா அவர்கள் ஒருமுறை இவர்களைக் சந்தித்த போது “தாங்கள் முந்தைய இலக்கிய வடிவங்களிலேயே கவிதை வடிக்கக் காரணம் என்ன? என்று விளக்கம் கேட்டார்.",
        description2:"அதற்கு இந்நூலின் ஆசிரியர் “எல்லோரும் புதுக்கவிதை மோகம் கொண்டு அலைந்தால் பழைய பாரம்பரியமிக்க – தனித்தன்மை கொண்ட பண்டைய இலக்கிய வடிவங்களைக் காப்பது யார்? அனைவரும் அதனைப் பார்க்காது முகம் திருப்பிகொண்டால் அது அழிந்து போகாதா? என்றார்கள். அதற்கு சாட்சியாக இவர்களின் உமர் ரலி புராணம், சித்திரக்கவி அமைந்த மகானந்தாலங்காரமாலை போன்ற நூற்கள் திகழ்கின்றன.144 பாக்கள் கொண்ட இந்த நூலில் பெருமானார் ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களின் திருவாழ்வு முழுவதும் சுருக்கமாக, உருக்கமாக இலக்கிய நயத்தோடு பதிவு செய்யப்பட்டு விட்டது. நாயகம் ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களின் வரலாறு கூறும் சீறாப் புராணத்தைப் படித்த திருப்தி இந்தச் சிறு நூலிலே கிடைக்கப்பெறும். புர்தா ஷரீபைப் படித்த புண்ணியம் இந்தப் புத்தகத்தின் வாயிலாகப் பூந்துவிடும். இதனைத் “தமிழில் இயற்றிய மௌலிது ஷரீபு” என்றாலும் பொருந்தமான கருத்தென்று புரிந்தவர்கள் ஒப்புக் கொள்வர்.",
