        name:'அற்புத அகில நாதர் ',
        price:120,
        short_description:"இஃது முஹம்மது நபி ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களை பற்றி, அவர்களின் திருப்பேரர் அஸ்ஸெய்யித் கலீல் அவ்ன் மௌலானா நாயகம் அவர்கள் எழுதியருளிய அழகிய கவிதைவடிவிலான தொகுப்பாகும்.",
        description1:"இஃது முஹம்மது நபி ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களை பற்றி, அவர்களின் திருப்பேரர் அஸ்ஸெய்யித் கலீல் அவ்ன் மௌலானா நாயகம் அவர்கள் எழுதியருளிய அழகிய கவிதைவடிவிலான தொகுப்பாகும்.இதில் பெருமானார் ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களின் வாழ்வில் நிகழ்ந்த அற்புதங்களை எல்லாம் அழகிய கவிதை திரட்டாக ஆசிரியர் அவர்கள் படைத்துள்ளார்கள். இஃது இசையமைத்து, பாடலாகவும் குறுந்தகடாகவும் வெளியிடப்படுள்ளது.மேலும் சென்னை காவல்துறைத் தலைவர் சு.ஸ்ரீபால், வா.மு. சேதுராமன் M.A., B.Ed., P.hd., D.Ltt.,, ஆகியோர்கள் முன்னுரை, அணிந்துரை வழங்கியுள்ளனர்.",
        description2:"மொத்தம் 132 கண்ணிகளாக இது வடிவமைக்கப்பட்டுள்ளது காண்க. சிறந்த சொல்லாடல், நாயகம் ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களுக்கு நிகழ்ந்த அற்புதங்களை அழகிய முறையில் எடுத்துவைக்கும் விதம், இதனின் இசைவடிவம் அனைத்தும் படிப்போரின், கேட்போரின் மனங்களை கொள்ளை கொள்ளும் என்பது தெளிவு. மொத்தம் 132 கண்ணிகளாக இது வடிவமைக்கப்பட்டுள்ளது காண்க. சிறந்த சொல்லாடல், நாயகம் ஸல்லல்லாஹூ அலைஹி வஸல்லம் அவர்களுக்கு நிகழ்ந்த அற்புதங்களை அழகிய முறையில் எடுத்துவைக்கும் விதம், இதனின் இசைவடிவம் அனைத்தும் படிப்போரின், கேட்போரின் மனங்களை கொள்ளை கொள்ளும் என்பது தெளிவு.",
