                                            <p class="single-product__short-desc">கருணாகர காரூண்யனான அல்லாஹ்வின்  திருநாமத்தைக் கொண்டு இதைத் தொடங்குகிறேன்.  அல்லாஹ்வுக்கே எல்லாப் புகழும்  அவனோ எமக்கு நேரானா பாதையை நேர்வழியாகக் காட்டியவன் *அதல்லாததை (கிதாபல்லாத)தை விட்டும் எம்மைப் பாதுகாத்தவன். பூர்வீகமான சிறப்புக்குறிய உபநாமங்களைக் கொண்டு அழைக்கப்படுபவன்.</p>
                                       
